.ticket-card {
  position: relative;
  overflow: hidden;
}

/* Top Circle */
.ticket-card::before,
.ticket-card::after {
  content: "";
  position: absolute;
  left: 11%;
  width: 30px;
  height: 30px;
  background: white;
  border: 1px dashed #ff3131;
  border-radius: 50%;
}

.ticket-card::before {
  top: -15px;
  z-index: 50;
}

/* Bottom Circle */
.ticket-card::after {
  bottom: -15px;
  z-index: 50;
}
